




























































































































































































































































import { Vue, Component, Watch } from "vue-property-decorator";
import AddOrEditDialog from "@/microApps/authority/views/businessObjectDefinition/components/AddOrEditDialog.vue";
import api from "../../api";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  filters: { formatterAttrType },
  components: {
    AddOrEditDialog
  }
})
export default class BusinessObjectDefinition extends Vue {
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "name"
    }
  };
  // 分页与筛选
  pagination: any = {
    query: {
      attrType: "",
      code: "",
      name: ""
    },
    current: 1,
    total: 0,
    tableData: [],
    size: 6
  };
  loading: any = false;
  // 是否显示功能按钮
  toolShow: boolean = false;
  // 对象定义类型基础表单
  basicForm: any = {
    name: "",
    code: "",
    icons: []
  };
  // 新增/编辑时表单验证规则
  basicFormRules: any = {
    name: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };
  // 业务对象基本类型表单
  baseForm: any = {};
  // 业务对象基本类型表单规则
  baseFormRules: any = {
    baseTypeName: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" },
    displayType: { required: true, trigger: "change", message: "请选择类别" }
  };
  // 业务对象定义类型表单
  typeDetailForm: any = {};
  // 业务对象类型下拉列表
  typeList: Array<any> = [
    {
      label: "点",
      value: "Point"
    },
    {
      label: "线",
      value: "Line"
    },
    {
      label: "面",
      value: "Area"
    }
  ];
  // 业务对象基础类型信息列表
  relationshipList: Array<any> = [];
  // 业务对象定义类型状态（0：基本类型下添加业务对象定义类型；1：业务对象类型下添加子节点；2：编辑当前业务对象类型）
  flagTypeDetail: number = 0;
  // 用于给子组件节点信息
  nodeData: any = {};

  switchValue: boolean = true;

  dialogVisible: boolean = false;
  attributesDialogVisible: boolean = false;
  // 是否可编辑
  editableStatus: boolean = false;

  created() {
    this.getObjectBaseTypeTree();
    this.baseList();
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }
  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }
  renderTreeStyle() {
    this.$nextTick(() => {
      const els: any[] = [...(document as any).getElementsByClassName("el-tree-node__content")];
      els
        .filter((i: any) => i.style.paddingLeft === "0px")
        .forEach((o: any) => {
          // o.style.marginBottom = "4px";
          o.style.boxShadow = "0px 2px 10px 0px rgba(155,164,182,0.41)";
          o.style.fontWeight = "bold";
          o.style.marginTop = "4px";
          o.style.backgroundColor = "rgba(255,255,255,1)";
          o.style.position = "relative";
          o.style.zIndex = 11;
        });
      els
        .filter((i: any) => i.style.paddingLeft !== "0px")
        .forEach((o: any, k: number) => {
          if (k % 2) {
            // o.style.backgroundColor = "rgba(246,247,250,1)";
          }
        });
    });
  }
  // 获取对象基础类型树
  async getObjectBaseTypeTree() {
    try {
      let {
        data: { data, status, message }
      } = await this.$http.get(api.businessObjectDefinition.baseGetObjectBaseTypeTree);
      if (status) {
        this.treeMessage.data = data;
        this.renderTreeStyle();
      } else {
        console.log(message);
      }
    } catch (e) {
      console.log(e);
    }

    //
    // this.$http.get(api.businessObjectDefinition.baseGetObjectBaseTypeTree).then((response: any) => {
    //   if (response.data.status) {
    //     this.treeMessage.data = response.data.data;
    //   }
    // });
  }
  // 树节点点击事件
  async handleNodeClick(data: any) {
    console.log("树节点点击");
    console.log(data);
    this.toolShow = !!data.leftNode;
    // 用来判断是否是一级节点（即有则为业务对象，无则为对象下的类型树）
    if (data.leftNode) {
      this.basicForm = Object.assign({}, data);
      if (this.basicForm.icon)
        this.basicForm.icons = [
          {
            name: "icon",
            url: this.basicForm.icon,
            smallUrl: this.basicForm.icon
          }
        ];
      this.editableStatus = false;
      this.nodeData = data;
      this.renderTreeStyle();
      this.typeDetailAttrPagingAttr();
    } else {
      this.baseForm = await this.baseGet(data.id);
    }
  }
  // 获取对象基础类型信息列表
  baseList() {
    this.$http.get(api.businessObjectDefinition.baseList).then((response: any) => {
      if (response.data.status) {
        this.relationshipList = response.data.data;
      }
    });
  }
  // 添加/编辑业务对象基本类型表单
  async openBaseForm(buttonName: string, data: any) {
    // this.$nextTick(() => {
    //   this.$refs["baseForm"] && (this.$refs["baseForm"] as any).resetFields();
    // });
    this.$refs["baseForm"] && (this.$refs["baseForm"] as any).resetFields();
    this.dialogVisible = true;
    switch (buttonName) {
      case "addTop":
        this.baseForm = {};
        break;
      case "add":
        break;
      case "edit":
        this.baseForm = await this.baseGet(data.id);
        break;
    }
  }
  // 查询一个对象基本类型
  async baseGet(id: string) {
    let {
      data: { status, data }
    } = await this.$http.get(api.businessObjectDefinition.baseGet + "/" + id);
    if (status) {
      return data;
    }
  }
  // 保存一个对象基础类型
  baseSave() {
    let url: string = "";
    if (this.baseForm.id) {
      url = api.businessObjectDefinition.baseUpdate;
    } else {
      url = api.businessObjectDefinition.baseAdd;
    }
    let ref: any = this.$refs["baseForm"];
    ref.validate((valid: any) => {
      if (valid) {
        this.$http.post(url, this.baseForm).then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message);
            this.getObjectBaseTypeTree();
            this.dialogVisible = false;
          } else {
            this.$message.error(response.data.message);
          }
        });
      }
    });
  }
  // 删除一个对象基本类型
  baseDelete(data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(api.businessObjectDefinition.baseDelete + "/" + data.id).then((response: any) => {
        if (response.data.status) {
          this.$message.success(response.data.message);
        } else {
          this.$message.error(response.data.message);
        }
        this.getObjectBaseTypeTree();
      });
    });
  }
  // 添加业务对象定义类型表单
  openTypeDetailForm(data: any, status: number) {
    console.log("添加事件");
    console.log(data);
    (this.$refs.tree as any).setCurrentKey(data.id);
    this.toolShow = true;
    this.flagTypeDetail = status;
    if (status === 0) {
      this.typeDetailForm.baseTypeId = data.id;
      this.typeDetailForm.parentId = null;
    } else if (status === 1) {
      this.typeDetailForm.baseTypeId = data.baseTypeId;
      this.typeDetailForm.parentId = data.id;
    }
    this.basicForm = {};
    this.basicForm.icons = [];
    this.pagination.tableData = [];
    this.editableStatus = true;
    console.log(this.basicForm);
  }
  // 将对象定义类型切换为编辑状态
  editTypeDetail() {
    this.editableStatus = true;
    this.flagTypeDetail = 2;
  }
  // 保存一个业务对象定义类型
  typeDetailSave(type: string) {
    let ref: any = this.$refs["basicForm"];
    ref.validate((valid: any) => {
      if (valid) {
        this.typeDetailBasicSave(type);
      }
    });
  }
  // 保存业务对象定义基本信息
  typeDetailBasicSave(type: string = "") {
    let tempVO = Object.assign(this.basicForm, this.typeDetailForm);
    if (tempVO.icons && tempVO.icons.length) {
      tempVO.icon = tempVO.icons[0].url ? tempVO.icons[0].url : "";
    } else {
      tempVO.icon = "";
    }
    this.$http.post(api.businessObjectDefinition.typeDetailAdd, tempVO).then(async (response: any) => {
      if (response.data.status) {
        this.nodeData = response.data.data;
        this.basicForm = response.data.data;
        if (type) {
          this.$message.success(response.data.message);
          this.editableStatus = false;
        } else {
          this.attributesDialogVisible = true;
        }
        await this.getObjectBaseTypeTree();
        (this.$refs["tree"] as any).setCurrentKey(response.data.data.id);
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 删除一个业务对象定义类型及其子节点
  typeDetailDelete(node: any, data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(api.businessObjectDefinition.typeDetailDelete + "/" + data.id).then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message);
          } else {
            this.$message.error(response.data.message);
          }
          this.getObjectBaseTypeTree();
        });
      })
      .catch(() => {});
  }
  // 获取类型的自定义属性
  typeDetailAttrPagingAttr() {
    this.loading = true;
    this.$http({
      method: "POST",
      url: api.businessObjectDefinition.typeDetailAttrPagingAttr + "/" + this.nodeData.id,
      data: this.pagination
    }).then((response: any) => {
      this.loading = false;
      if (response.data.status) {
        this.pagination.tableData = response.data.data.records;
        this.pagination.total = response.data.data.total;
      }
    });
    // this.$http.get(api.businessObjectDefinition.typeDetailAttrPagingAttr + "/" + this.typeDetailId, {
    //   params: {
    //     pageBeanSearch: this.pagination
    //   }
    // }).then((response: any) => {
    //   console.log(response)
    // })
  }
  // 业务对象定义类型添加属性
  typeDetailAttrAdd() {
    let ref: any = this.$refs["basicForm"];
    ref.validate((valid: any) => {
      if (valid) {
        this.typeDetailBasicSave();
      }
    });
  }
  // 关闭业务对象属性弹窗
  closeAttributesDialog() {
    this.attributesDialogVisible = false;
    this.typeDetailAttrPagingAttr();
  }
  // 是否必填改变
  mustInputChange = (row: any) => {
    this.$http.post(api.businessObjectDefinition.typeDetailAttrUpdate, row);
  };
  //删除自定义属性
  deleteAttr(attrId: string) {
    let _this = this;
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(api.businessObjectDefinition.typeDetailAttrDelete + "/" + attrId).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              this.typeDetailAttrPagingAttr();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }
  //上移自定义属性
  upAttrSort(attr: any, index: any) {
    const idx = index + this.pagination.size * (this.pagination.current - 1);
    if (idx > 0) {
      this.$http.post(api.businessObjectDefinition.typeDetailAttrUpAttrSort, attr).then((res: any) => {
        if (res.data.status) {
          this.$message.success("操作成功");
          this.typeDetailAttrPagingAttr();
        } else {
          this.$message.error(res.data.message);
        }
      });
    } else {
      this.$message.info("已经是第一条");
    }
  }
  //下移自定义属性
  downAttrSort(attr: any, index: any) {
    const idx = index + this.pagination.size * (this.pagination.current - 1);
    if (idx < this.pagination.total - 1) {
      this.$http.post(api.businessObjectDefinition.typeDetailAttrDownAttrSort, attr).then((res: any) => {
        if (res.data.status) {
          this.$message.success("操作成功");
          this.typeDetailAttrPagingAttr();
        } else {
          this.$message.error(res.data.message);
        }
      });
    } else {
      this.$message.info("已经是最后一条");
    }
  }
}
