














































































































































import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "../../../api";

@Component({})
export default class AddOrEditDialog extends Vue {
  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;
  @Prop()
  data!: any;

  // 新增/编辑属性弹窗显示/隐藏
  addOrEditDialogVisible: boolean = false;
  // 是否新建属性
  isNew: boolean = false;
  transferData: any = {
    data: [],
    selected: [],
    defaultProps: {
      key: "id",
      label: "attrName",
      code: "code"
    }
  };

  optionItem: any = {
    attrName: "",
    code: "",
    attrType: "",
    id: "",
    mustInput: 0,
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };
  optionItemFormRules: any = {
    attrName: { required: true, trigger: "blur", message: "请输入名称" },
    code: { required: true, trigger: "blur", message: "请输入编码" }
  };

  typeOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "textarea",
      label: "多行文本"
    },
    {
      value: "location",
      label: "定位"
    },
    // {
    //   value: "eventType",
    //   label: "事件类型"
    // },
    {
      value: "link",
      label: "网页链接"
    }
  ];

  @Watch("dialogVisible", { immediate: true, deep: true })
  visibleChange(newValue: boolean) {
    this.addOrEditDialogVisible = newValue;
    if (newValue) {
      this.$nextTick(() => {
        (this.$refs["transfer"] as any).clearQuery("left");
        (this.$refs["transfer"] as any).clearQuery("right");
      });
    }
  }
  // 获取穿梭框数据
  @Watch("data", { immediate: true, deep: true })
  async getTransferData(nodeData: any) {
    let {
      data: { data, status }
    } = await this.$http.post(api.businessObjectDefinition.typeDetailAttrTransferData, {
      typeDetailId: nodeData.id,
      baseTypeId: nodeData.baseTypeId
    });
    if (status) {
      this.transferData.data = data.data;
      this.transferData.selected = data.selected;
    }
    // this.$http
    //     .post(api.businessObjectDefinition.typeDetailAttrTransferData, {
    //       typeDetailId: this.data.id,
    //       baseTypeId: this.data.baseTypeId
    //     })
    //     .then((response: any) => {
    //       if (response.data.status) {
    //         this.transferData.data = response.data.data.data;
    //         this.transferData.selected = response.data.data.selected;
    //       }
    //     });
  }

  saveAttr() {
    console.log(this.transferData);
    console.log(this.data);
    let tempVO = Object.assign({}, { attrList: this.transferData.selected }, { typeDetailId: this.data.id });
    console.log(tempVO);
    this.$http.post(api.businessObjectDefinition.typeDetailAttrBind, tempVO).then((response: any) => {
      if (response.data.status) {
        this.$message.success(response.data.message);
        this.$emit("closeDialog");
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  // 查询
  search() {}

  // 展开新建业务对象属性Panel
  openPanel() {
    this.$nextTick(() => {
      this.isNew = !this.isNew;
      // console.log(this.$refs["optionItemForm"]);
      // this.$refs["optionItemForm"] && (this.$refs["optionItemForm"] as any).resetFields();
      this.optionItem = {
        attrName: "",
        code: "",
        attrType: "",
        // id: "",
        mustInput: 0,
        dataRange: {
          options: [],
          multiSelectedItems: []
        }
      };
    });
  }

  // 数据项类型修改
  typeChange() {
    if (this.optionItem.attrType === "select" || this.optionItem.attrType === "multiSelect") {
      this.optionItem.dataRange.options = [];
      this.optionItem.dataRange.multiSelectedItems = [];
    } else if (this.optionItem.attrType === "number") {
      this.$set(this.optionItem.dataRange, "numMin", 0);
      this.$set(this.optionItem.dataRange, "numMax", 10);
      this.$set(this.optionItem.dataRange, "unit", "");
    } else if (this.optionItem.attrType === "text") {
      this.$set(this.optionItem.dataRange, "presetText", "");
    } else if (this.optionItem.attrType === "image") {
      this.$set(this.optionItem.dataRange, "imgMaxNum", 1);
    } else if (this.optionItem.attrType === "location") {
      // this.changeMustInputStatus(this.optionItem.mustInput);
    }
  }

  addOptionItem() {
    if (this.optionItem.attrType === "select" || this.optionItem.attrType === "multiSelect") {
      this.optionItem.dataRange.options.push({
        content: "",
        defaultChosen: false
      });
    } else {
      this.optionItem.dataRange.options.push({});
    }
  }

  removeOptionItem(index: number) {
    this.optionItem.dataRange.options.splice(index, 1);
  }

  handleCheck(index: number) {
    if (this.optionItem.dataRange.options[index].defaultChosen && this.optionItem.attrType === "select") {
      for (let i in this.optionItem.dataRange.options) {
        this.optionItem.dataRange.options[i].defaultChosen = false;
      }
      this.optionItem.dataRange.options[index].defaultChosen = true;
    }
  }

  handleAddItem() {
    console.log(JSON.parse(JSON.stringify(this.optionItem)));
    console.log(this.data);
    let temp = JSON.parse(JSON.stringify(this.optionItem));
    temp.dataRange = JSON.stringify(temp.dataRange);
    let tempVO = Object.assign({}, temp, { baseTypeId: this.data.baseTypeId });
    console.log(temp);
    console.log(tempVO);
    let ref: any = this.$refs["optionItemForm"];
    ref.validate((valid: any) => {
      if (valid) {
        this.$http.post(api.businessObjectDefinition.typeDetailAttrAddAttr, tempVO).then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message);
            this.transferData.data.push(response.data.data);
            this.transferData.selected.push(response.data.data.id);
            // this.getTransferData().then(() => {
            //   this.transferData.selected.push(response.data.data.id);
            // });
            this.isNew = false;
          } else {
            this.$message.error(response.data.message);
          }
        });
      }
    });
  }

  // 关闭弹窗
  closeDialog() {
    this.addOrEditDialogVisible = false;
    this.$emit("closeDialog", this.transferData);
  }

  handleSizeChange() {}
  handleCurrentChange() {}
}
